import { Typography } from '@mui/material';
import { useState } from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Home from './components/Home';
import Play from './components/Play';
import Results from './components/Results';

function App() {

  const [questions, setQuestions] = useState({
    easy: [
      { question: "Olet veneretkell saaressa. Saako rannalle tehd nuotion ilman lupaa?", answer: "EI", explanation: "Tulenteko ei kuulu jokaisen oikeuksiin. Tulen voit tehd virallisilla tulentekopaikoilla." },
      { question: "Olet veneilemss eik lhistll ny yhtkn saarta tai venett. Voit heitt mereen pienen muoviroskan, koska se kuitenkin hukkuu meren aaltoihin.", answer: "EI", explanation: "Luontoon ei saa heitt roskia. Muovi ei maadu vaan kerntyy mereen." },
      { question: "Saako Itmerell onkia ilman kalastuslupaa.", answer: "KYLL", explanation: "Onkiminen kuuluu jokaisenoikeuksiin. Onkia voi ilman lupaa, paitsi erityisill suojelualuilla." },
      { question: "Itmerell saa veneill ilmaiseksi.", answer: "KYLL", explanation: "Vesialueilla liikkuminen kuuluu jokaisenoikeuksiin. Kunhan liikut hiritsemtt muita ihmisi tai luontoa." },
      { question: "Onko uiminen sallittua muuallakin kuin virallisilla uimarannoilla?", answer: "KYLL", explanation: "Uida saa kaikkialla, miss muutenkin saa liikkua." },
      { question: "Olet retkell Ristisaaressa ja haluat tehd tulentekopaikalle tulen, mutta polttopuuta ei ny missn. Onneksi saaressa saa kaataa puita ja katkoa oksia polttopuuksi.", answer: "EI", explanation: "Puiden kaato tai oksien katkominen on kielletty. Mys saarissa." },
      { question: 'Saarella on kyltti: "Luonnonsuojelualue, maihinnousu kielletty 1.4.-31.7." Saako saareen keskuussa rantautua.', answer: "EI", explanation: "Kielto koskee mys keskuuta. Kiellolla pyritn suojelemaan linnuston pesint ja sit on syyt noudattaa." },
      { question: "Olet perheen kanssa Lehmsaaressa tulentekopaikalla paistamassa makkaraa, kun toinen porukka saapuu paikalle. Toisten pit kunnioittaa rauhaanne ja jd odottamaan tulentekopaikan ulkopuolelle.", answer: "EI", explanation: "Teidn tulee ottaa huomioon muut luonnossaliikkujat. Tulipaikalla on tilaa kaikille!"},
      { question: "Kukourissa kydesssi huomaat valkoposkihanhien ja lokkien pesi kaikkialla. Haluaisit kierrell linnoituksella, mutta pest ovat edess. Pitk sinun vlitt pesivist linnuista?", answer: "KYLL", explanation: "Lintujen pesint ei saa hirit, vaikka pest olisivat omasta mielestsi vrss paikassa."},
      { question: "Saako saarilla kert pihlajanmarjoja, vaikka ne olisivat puussa viel kiinni?", answer: "KYLL", explanation: "Marjoja saa poimia. Ei ole vli ovatko marjat puussa vai varvuissa kiinni."}
    ],
    hard: [
      { question: "Net veneest maissa yksinisen pienen laiturin, joka ei ole kenenkn pihapiiriss. Laiturissa ei ole kyltti eik ole tietoa kenen se on. Saat kiinnitty laituriin ja nousta sille.", answer: "KYLL", explanation: "Laituria saa kytt ellei sen omistaja ole sit erikseen kieltnyt esim. kyltill." },
      { question: "Olet lytnyt erlt saarelta kivan rantasataman kalliolta. Rantaudut sille usein ja mieleesi tulee pultin kiinnitys kallioon. Pysyvn porapultin saa kiinnitt tyhjlle kalliolle ilman maanomistajan lupaa rantautumisen helpottamiseksi.", answer: "EI", explanation: "Pysyvn porapultin asentamiseen tarvitaan maanomistajan suostumus." },
      { question: "Onko uistelukalastus kielletty Itisen Suomenlahden kansallispuiston alueella?", answer: "EI", explanation: "Kansallispuiston alueella viehekalastamista koskevat normaalit snnt ja rajoitukset." },
      { question: "Voiko maihinnousun saareen est maanomistajan kieltokyltill ilman erityist perustetta?", answer: "EI", explanation: "Luonnonsuojelulaki (5 luku 36) kielt lakiin nojautumattoman maihinnousun kieltvn kyltin asettamisen." },
      { question: "Piha-alueiden rannasta on 50 metrin suojavyhyke veteen pin, jossa veneet eivt saa liikkua eik ankkuroitua.", answer: "EI", explanation: "Tmnkaltaista suojavyhykett ei ole olemassa. Vesialueella liikkumisesta ei kuitenkaan saa aiheutua tarpeetonta haittaa tai hirit. Kotirauhaa ei myskn saa rikkoa." },
      { question: "Saako risukeitint kytt saaressa ruohikkopalovaroituksen aikana?", answer: "EI", explanation: "Risukeittimien kytt ei ole sallittua ruohikko- ja metspalovaroituksen aikana siit aiheutuvan kipinintivaaran takia."},
      { question: "Kytte englantilaisen Sarahin kanssa veneilemss Itmerell ja kiertelemss saaria. Saako Sarah kert saarilta puolukoita myyntiin, vaikka on kotoisin EU:n ulkopuolelta?", answer: "KYLL", explanation: "Luonnontuotteiden kerminen kuuluu jokaisenoikeuksiin tarkoitukseen tai kansallisuuteen katsomatta."},
      { question: "Saako hailia litkata ilman kalastuslupaa?", answer: "KYLL", explanation: "Hailia eli silakkaa saa kalastaa ns. silakkalitkalla eli yhdell vavalla siimaan kiinnitetyill pystysuunnassa liikuteltavilla koukuilla."},
      { question: "Saako meren jlle tehd avotulen?", answer: "KYLL", explanation: "Jll (toisin kuin maalla) saa tehd nuotion."},
      { question: "Rantaudut juhannuksena asumattomalle saarelle. Saako koiran pst vapaaksi saareen?", answer: "EI", explanation: "Koiraa ei saa pit vapaana 1.3.-19.8. vlisen aikana, eik muutenkaan ilman maanomistajan lupaa. Koirien kiinnipitoajan kunnioittaminen on trke, jotta luonnonelimill olisi rauha niiden lisntymisajan aikana. Vaikka koira ei olisikaan esimerkiksi linnuista kiinnostunut, linnut suhtautuvat koiraan kuin mihin tahansa petoon, ja kaikkoavat helposti pesimpaikalta."},
      { question: "Laitesukeltaminen vaatii aina luvan, ellet ole sukellusseuran jsen.", answer: "EI", explanation: "Sukeltaminen on vesialueiden yleiskytt ja jokaisen oikeus. Sukeltamista koskevia rajoituksia on lhinn rajavyhykkeill, suojelualueilla ja rauhoitettujen hylkyjen lheisyydess."}
    ],
    error: ["error"]
  }
  );

  const [numberOfQuestions, setNumberOfQuestions] = useState(8);
  const [questionLevel, setQuestionLevel] = useState("error");
  const [userScore, setUserScore] = useState(0);
  const [userTime, setUserTime] = useState();

  const getInitialScore = () => {
    const storedScore = localStorage.getItem("scores");
    return storedScore ? JSON.parse(storedScore) : { easy: [], hard: [] };
  }

  const [scores, setScores] = useState(getInitialScore());

  const updateScores = (newScore, level) => {
    let newScores = { ...scores };
    if (level === "easy") {
      newScores.easy.push(newScore);
    }
    if (level === "hard") {
      newScores.hard.push(newScore);
    }
    setScores({...newScores});
    localStorage.setItem("scores", JSON.stringify(scores));
  }

  return (

    <HashRouter>

      <Typography
        fontSize="2.8rem"
        variant="h1"
        align='center'
        sx={{ mb: 5 }}
      >Jokaisenoikeudet Itmerell
      </Typography>
      <Routes>
        <Route
          path="/"
          element={<Home
            setQuestionLevel={setQuestionLevel}
            questionLevel={questionLevel}
            questions={questions}
            scores={scores}
          />} />
        <Route
          path="/play"
          element={<Play
            questions={questions}
            numberOfQuestions={numberOfQuestions}
            setUserTime={setUserTime}
            setUserScore={setUserScore}
            questionLevel={questionLevel}
          />} />
        <Route
          path="/results"
          element={<Results
            numberOfQuestions={numberOfQuestions}
            scores={scores}
            updateScores={updateScores}
            userScore={userScore}
            userTime={userTime}
            setUserTime={setUserTime}
            setUserScore={setUserScore}
            questionLevel={questionLevel}
          />} />
      </Routes>

    </HashRouter>

  );
}

export default App;
